

//   leave-request   //

.leave-request {
  padding-top: 122px;
  padding-bottom: 117px;
  background-image: url("/assets/images/main/block4/bg.jpg");
  background-position: center;
  background-size: cover;
  position: relative;
  overflow: hidden;
  @media (max-width: 1023.98px) {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  @media (max-width: 767.98px) {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  .main-title .title p {
    letter-spacing: 0.2px;
  }

  &__content {
    display: flex;
  }

  &__description {
    width: 785px;
    padding-left: 4px;
    @media (max-width: 1023.98px) {
      width: 385px;
    }
    @media (max-width: 767.98px) {
      max-width: 385px;
      width: 100%;
    }
  }

  &__info {
    @media (max-width: 767.98px) {
      p {
        font-size: 14px;
        line-height: 18px;
      }
    }
  }

  &__photo-block {
    position: relative;
    width: 200px;
  }

  &__photo {
    position: absolute;
    top: -50px;
    left: 0;
    width: 611px;
    height: 510px;
    @media (max-width: 1440.98px) {
      width: 480px;
      height: auto
    }
    @media (max-width: 1023.98px) {
      width: 450px;
      top: 70px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__info {
    margin-top: 20px;
  }
}
.leave-request__last .leave-request__photo{
  width: 580px;
  height: auto;
  top: -83px;
  left: 100px;
}
.main-form {
  display: flex;
  margin-top: 53px;
  flex-wrap: wrap;

  .form__row {
    width: 165px;
    margin-right: 30px;
    @media (max-width: 1023.98px) {
      width: 100%;
      margin-right: 0;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  .form__send {
    width: 360px;
    height: 50px;
    margin-top: 22px;

    .button-send {
      width: 100%;
    }
  }
}

